/* General Styling */
body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  overflow-x: hidden; /* Prevent horizontal scrolling */
  box-sizing: border-box;
  background-color: #f8f9fa; /* Set background color for the entire page */
}

*, *::before, *::after {
  box-sizing: inherit; /* Ensure consistent sizing */
}

/* Center the container and remove excess space */
#plans .container {
  margin: 0 auto;
  max-width: 1200px;
  padding: 0 15px;
  display: flex;
  flex-wrap: wrap; /* Ensures responsive wrapping */
  justify-content: center; /* Centers the cards */
}

/* Selected Plan Styling */
.selected-plan-container {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
}

.selected-plan-card {
  max-width: 600px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.selected-plan-card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

/* Plan Cards */
#plans .card {
  cursor: pointer;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  margin: 15px; /* Add spacing between cards */
  width: 100%;
  max-width: 350px; /* Restrict card size */
}

#plans .card:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
}

.plans-page {
  background-color: #f8f9fa;
  text-align: center; /* Ensures consistent centering */
  padding: 0; /* Remove default padding */
  margin: 0; /* Remove default margin */
}

/* Hero Section */
header {
  background: linear-gradient(to bottom, #007bff, #0056b3);
  color: white;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.3);
  text-align: center; /* Centers content */
  padding: 60px 20px;
  margin: 0; /* Remove margin */
  height: 100vh;
}

/* Cards */
.card {
  background-color: #007bff; /* Blue background color */
  color: #fff; /* White text for contrast */
  border: none; /* Remove border */
  border-radius: 10px; /* Optional: Add rounded corners */
  transition: transform 0.3s, box-shadow 0.3s; /* Smooth hover effect */
}

.card:hover {
  transform: scale(1.05); /* Slightly enlarge on hover */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow on hover */
}

.card-title {
  font-weight: bold;
}

.card-body p {
  margin: 0;
  font-size: 16px;
  color: #555;
}

.card.border-primary {
  border: 2px solid #007bff;
}

/* Footer */
footer {
  text-align: center;
  font-size: 14px;
  background-color: #333;
  color: #ccc;
  padding: 20px 0;
  margin: 0; /* Remove margin */
}

/* Modal Overlay */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* Dim background */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1050; /* Higher than other elements */
}

/* Modal Content */
.modal-content {
  background: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.5);
  max-width: 500px;
  width: 90%; /* Responsive for smaller screens */
  position: relative; /* Needed for the close button */
}

/* Close Button */
.close-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 18px;
  line-height: 1;
  cursor: pointer;
  border: none;
  background: none;
}

/* Responsive Design */
@media (max-width: 768px) {
  #plans .card {
    flex: 1 1 calc(50% - 30px); /* Two-column layout for smaller screens */
  }
}

@media (max-width: 480px) {
  #plans .card {
    flex: 1 1 100%; /* Single-column layout for very small screens */
    margin: 10px 0; /* Adjust margin for small screens */
  }
}
.navbar-logo {
  height: 40px; /* Adjust the size as needed */
  margin-right: 10px; /* Space between the logo and text */
  vertical-align: middle; /* Align the image with the text */
}