/* Center the login container on the screen */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh; /* Full viewport height */
  background-color: #f5f5f5; /* Light background */
  font-family: Arial, sans-serif; /* Clean font */
}

/* Card styling */
.login-card {
  background: white;
  padding: 2rem;
  border-radius: 10px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  max-width: 400px;
  width: 100%;
  text-align: center;
}

/* Title */
.login-card h2 {
  margin-bottom: 1rem;
  font-size: 1.5rem;
  color: #333;
}

/* Form styling */
.login-form {
  display: flex;
  flex-direction: column;
}

/* Input fields */
.form-input {
  padding: 0.8rem;
  margin: 0.5rem 0;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 1rem;
}

/* Input focus styling */
.form-input:focus {
  border-color: #007bff;
  outline: none;
  box-shadow: 0 0 5px rgba(0, 123, 255, 0.5);
}

/* Login button */
.login-button {
  background-color: #007bff;
  color: white;
  border: none;
  padding: 0.8rem;
  margin-top: 1rem;
  border-radius: 5px;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

/* Button hover effect */
.login-button:hover {
  background-color: #0056b3;
}
.form-label {
  display: block; /* Ensure the label is visible */
  color: black;   /* Set a visible color */
  font-size: 1rem;
}


/* Error message */
.error-message {
  margin-top: 1rem;
  color: #ff4d4f;
  font-size: 0.9rem;
  text-align: left;
}
@media (max-width: 768px) {
  .cards-container {
      flex-direction: column;
      align-items: center;
  }

  .card {
      width: 90%;
  }
}